import './App.css';
import React from 'react';
import { GetBasicModel } from './Models/basicModel'
import { SusceptibleRatex2Model } from './Models/susceptibleRatex2Model'
import { BigModel } from './Models/bigModel'

function App() {

  return (
    <div className="App" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', marginTop: 75 }}>
      <div style={{ width: '60%', }}>
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start', height: 200, }}>
          <h1>Insights from multiple pandemic simulators</h1>
          <h3 style={{ marginTop: -10 }}>An interactive report by Anh Chau</h3>
        </div>

        <div style={{ display: 'flex', flex: 1, flexDirection: 'column' }}>

          <div style={{ display: 'flex', flex: 0.1, flexDirection: 'row', marginBottom: 50 }}>
            <div style={{ display: 'flex', flex: 0.5, flexDirection: 'column', textAlign: 'start', flexWrap: 'wrap' }}>
              <h2>Basic model</h2>
              <span>The basic models show how the disease spread with no restriction on how people react</span>  
              <text>The default settings are:</text>
              <ol>
                <li>Population: 52</li>
                <li>Initial infected percentage: 10%</li>

                <li>susceptible rate: 20%</li>
                <li>Contagious radius: 1m</li>
                <li>Recovery time: 3s</li>
              </ol>
              <i>(To get a fully customizable model with a graphical report, visit my other pandemic simulator project <a href={'https://tuananhc.github.io/pandemic_simulator/'}>here</a>)</i>
              As you can see, this is a pretty oversimplified version of a community, but it serves as a good starting point for our research
              <p>The next few models will experiment how changes affect the spreading speed of the disease</p>
            </div>
            <div style={{ display: 'flex', flex: 0.5, justifyContent: 'flex-end' }}>
              {GetBasicModel()}
            </div>
          </div>

          
          <div style={{ display: 'flex', flex: 0.1, flexDirection: 'row' }}>
            <div style={{ display: 'flex', flex: 0.5, flexDirection: 'column', textAlign: 'start', flexWrap: 'wrap' }}>
              <p>The basic models show how the disease spread with no restriction on how people react</p>  
              <text>The default settings are:</text>
              <ol>
                <li>Population: 52</li>
                <li>Initial infected percentage: 10%</li>
                <li>susceptible rate: 20%</li>
                <li>contagious radius: 1m</li>
                <li>Recovery time: 3s</li>
              </ol>
              <p>(To get a fully customizable model with a graphical representation, visit my other pandemic simulator project <a href={'https://tuananhc.github.io/pandemic_simulator/'}>here</a>)</p>
            </div>
            <div style={{ display: 'flex', flex: 0.5, justifyContent: 'flex-end' }}>
              {GetBasicModel()}
            </div>
          </div>

          
          <div style={{ display: 'flex', flex: 0.1, flexDirection: 'row' }}>
            <div style={{ display: 'flex', flex: 0.5, flexDirection: 'column', textAlign: 'start', flexWrap: 'wrap' }}>
              <p>The basic models show how the disease spread with no restriction on how people react</p>  
              <text>The default settings are:</text>
              <ol>
                <li>Population: 52</li>
                <li>Initial infected percentage: 10%</li>
                <li>susceptible rate: 20%</li>
                <li>contagious radius: 1m</li>
                <li>Recovery time: 3s</li>
              </ol>
              <p>(To get a fully customizable model with a graphical representation, visit my other pandemic simulator project <a href={'https://tuananhc.github.io/pandemic_simulator/'}>here</a>)</p>
            </div>
            <div style={{ display: 'flex', flex: 0.5, justifyContent: 'flex-end' }}>
              {SusceptibleRatex2Model()}
            </div>
          </div>

          {BigModel()}

        </div>
      </div>
    </div>
  );
}

export default App;
